<template>
  <div>
    <ToolBar>
      <div>
        <el-input
          placeholder="用户名"
          size="small"
          style="width: 140px"
          v-model="searchParams.commentMemberName"
          clearable
        ></el-input>

        <el-input
          placeholder="商品名"
          size="small"
          style="width: 140px"
          v-model="searchParams.goodsName"
          clearable
        ></el-input>

        <el-date-picker
          v-model="searchParams.dateRange"
          size="small"
          type="datetimerange"
          :picker-options="pickerOptions"
          range-separator="至"
          start-placeholder="开始日期"
          end-placeholder="结束日期"
          align="right"
        ></el-date-picker>
        <el-button type="success" size="small" @click="refresh()">查询</el-button>
        <el-button type="warning" size="small" @click="clearSearchParams()">重置</el-button>
      </div>
    </ToolBar>
    <el-table
      :data="tableData"
      border
      style="width: 100%"
      :row-style="{height:'8px'}"
      :cell-style="{padding:'0px 0px'}"
    >
      <el-table-column type="expand">
        <template slot-scope="props">
          <el-form label-position="left">
            <el-form-item label="订单号">
              <span>{{ props.row.commentOrderSn }}</span>
            </el-form-item>
            <el-form-item label="创建时间">
              <span>{{ props.row.createTime }}</span>
            </el-form-item>
            <el-form-item label="更新时间">
              <span v-html=" props.row.updateTime"></span>
            </el-form-item>
            <el-form-item label="更新人">
              <span>{{ props.row.updateBy }}</span>
            </el-form-item>
            <el-form-item label="用户提交截图" v-if="props.row.commentPicObj">
              <el-image
                style="width: 100px; height: 100px;margin-left:5px"
                :src="item"
                fit="fill"
                v-for="(item,index) in props.row.commentPicObj"
                :key="index"
              ></el-image>
            </el-form-item>
          </el-form>
        </template>
      </el-table-column>
      <el-table-column align="center" prop="commentMemberName" label="用户名"></el-table-column>
      <el-table-column align="center" prop="goodsName" label="商品名"></el-table-column>
      <el-table-column align="center" prop="commentStar" label="好评度"></el-table-column>
      <el-table-column align="center" prop="commentText" label="评论内容"></el-table-column>
      <el-table-column align="center" prop="noticeStatus" label="状态" width="130">
        <div slot-scope="s">
          <el-select
            v-model="s.row.commentExamStatus"
            placeholder="请选择"
            size="mini"
            @change="changeStatus(s.row)"
          >
            <el-option
              v-for="(k,v,index) in commentExamStatusEnum"
              :key="index"
              :label="k"
              :value="v*1"
            ></el-option>
          </el-select>
        </div>
      </el-table-column>
      <el-table-column align="center" prop="createTime" label="创建时间"></el-table-column>
    </el-table>
    <Pagination
      :params="searchParams"
      :requestFunc="getCommentList"
      ref="pagination"
      @returnData="returnData"
    />
    <UserEdit
      :title="titie"
      ref="edit"
      :showEditDialog="showEditDialog"
      @close="showEditDialog = false"
      @success="refresh"
    />
  </div>
</template>

<script>
import { getCommentList, changeCommentStatus } from "@/api/user/userManage";
import { getDateList } from "@/utils/date";
import { CommentExamStatusEnum } from "@/utils/Immutable";
import { resetObject } from "@/utils/common";
import UserEdit from "./UserEdit";
export default {
  components: { UserEdit },
  data() {
    return {
      searchParams: {
        commentMemberName: "",
        goodsName: "",
        dateRange: []
      },
      getCommentList: getCommentList,
      titie: "",
      total: 10,
      pickerOptions: getDateList(),
      showEditDialog: false,
      tableData: [],
      commentExamStatusEnum: CommentExamStatusEnum
    };
  },
  methods: {
    clearSearchParams() {
      resetObject(this.searchParams);
      this.refresh();
    },
    changeStatus(v) {
      changeCommentStatus({ id: v.id, commentExamStatus: v.commentExamStatus });
    },
    refresh() {
      this.$refs.pagination.Refresh(); //分页刷新
      this.showEditDialog = false;
    },
    returnData(data) {
      this.tableData = data.map(v => {
        v.commentPicObj = v.commentPic ? JSON.parse(v.commentPic) : [];
        return v;
      });
    },
    edit(row, type) {
      this.titie = type == "recharge" ? "手动充值" : "手动扣款";
      this.showEditDialog = true;
      this.$refs.edit.update(row.id, type);
    }
  }
};
</script>
