<template>
  <div>
    <el-dialog
      @opened="openDialog"
      :width="dialogWidth"
      :title="title"
      :visible.sync="showEditDialog"
      :show-close="false"
      :close-on-click-modal="false"
    >
      <el-form
        :model="forms"
        :rules="rules"
        ref="editForms"
        class="edit-forms"
        label-position="left"
        :label-width="labelWidth"
      >
        <el-form-item label="充值金额" prop="balance" v-if="forms.type=='recharge'">
          <el-input v-model="forms.balance" placeholder="类型名称"></el-input>
        </el-form-item>
        <el-form-item label="扣款金额" prop="balance" v-else>
          <el-input v-model="forms.balance" placeholder="类型名称"></el-input>
        </el-form-item>
      </el-form>

      <div slot="footer" class="dialog-footer">
        <el-button @click="$emit('close')">取 消</el-button>
        <el-button type="primary" @click="submit">确认</el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
import { Required, Float2And0 } from "@/utils/validateRules";
import { manualAmount } from "@/api/user/userManage";
import { fillerLeft, resetObject } from "@/utils/common";

export default {
  props: {
    title: {
      type: String,
      default: "编辑"
    },
    dialogWidth: {
      type: String,
      default: "700px"
    },
    labelWidth: {
      type: String,
      default: "120px"
    },
    showEditDialog: Boolean
  },
  data() {
    return {
      forms: {
        id: "",
        balance: 0,
        type: ""
      },
      rules: {
        balance: [Float2And0, Required]
      }
    };
  },
  methods: {
    submit() {
      this.$refs.editForms.validate(valid => {
        if (valid) {
          manualAmount(this.forms).then(data => {
            if (data.code == 0) {
              this.$message.success("更新成功！");
              this.$emit("success");
              return;
            }
            this.$message.error(data.msg);
          });
          return;
        }
        this.$message.error("请按照提示正确填写内容！");
        return false;
      });
    },
    //任务图标文件上传成功后
    openDialog() {},

    update(id, type) {
      //可以外部填充回写做编辑用，也可以请求详情接口填充表单
      this.$set(this.forms, "id", id);
      this.$set(this.forms, "balance", 0);
      this.$set(this.forms, "type", type);
    }
  }
};
</script>
