import request from "@/utils/request.js";
/**
 * 登录
 * @param params
 * @returns {AxiosPromise}
 */
export function getUserList(params) {
    return request({

        url: "/apiadmin/member/findlist",
        method: "get",
        params: params //注意：如果是get请求请使用 params: params
    });
}
export function manualAmount(params) {
    return request({
        url: "/apiadmin/member/manualamount",
        method: "post",
        data: params //注意：如果是get请求请使用 params: params
    });
}

export function getCommentList(params) {
    return request({
        url: "/apiadmin/comment/findlist",
        method: "get",
        params: params //注意：如果是get请求请使用 params: params
    });
}

export function changeCommentStatus(params) {
    return request({
        url: "/apiadmin/comment/edit",
        method: "post",
        data: params //注意：如果是get请求请使用 params: params
    });
}
export function statisticUserNum() {
    return request({
        url: "/apiadmin/member/staticsticusernum",
        method: "get",
    });
}